.header {
  background-color: #fff;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000001a;
}

.logo {
  margin-left: 50px;
}

.logoimg {
  vertical-align: middle;
}

.nav {
  margin-right: 50px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  margin-right: 20px;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link.is-active {
  color: #00acdc;
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__item:last-child .nav__link {
  margin-right: 0;
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding-top: 32px;
}

.card__img {
  width: 100%;
  height: 134px;
  padding: 0 19px;
  display: block;
}

.card__info-wrapper {
  padding: 40px 16px 16px;
}

.card__title {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  padding-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  padding-top: 16px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  padding-top: 24px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 38px;
  text-decoration: none;
  display: block;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.reviews {
  color: #060b35;
  padding-top: 1px;
  font-size: 10px;
  line-height: 14px;
}

* {
  box-sizing: border-box;
  margin: 0;
  transition: all .3s;
}

body {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

.main {
  grid-template-rows: repeat(2, min-content);
  grid-template-columns: repeat(1, min-content);
  justify-content: center;
  gap: 46px 48px;
  height: calc(100vh - 60px);
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, min-content);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, min-content);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, min-content);
  }
}

